import NotificationHelper from './notification-helper';
import CONFIG from '../globals/config';
import { filterContacts } from '..';

const WebSocketInitiator = {
  init(url) {
    const webSocket = new WebSocket(url);
    webSocket.onmessage = this.onMessageHandler;
  },

  onMessageHandler(message) {
    const restaurant = JSON.parse(message.data);

    NotificationHelper.sendNotification({
      title: `${restaurant.title} is on cinema!`,
      options: {
        body: restaurant.overview,
        image: `${CONFIG.BASE_IMAGE_URL + restaurant.poster_path}`,
      },
    });
  },
};

export default WebSocketInitiator;
restaurants.addEventListener('submit', (event) => {
  event.preventDefault();
  retaurantsContainer.innerHTML = '';

  import('lodash.filter')
    .then((module) => module.default)
    .then(filterContacts)
    .catch((error) => alert(error));
});
